export default ({ scenes = [] }) => {
  let currentScene = null;

  return {
    loadScene: props => {
      const { areaId } = props;

      if (currentScene) currentScene.stop();

      const { sceneObject: Scene } = scenes.find(
        scene => scene.areaId === areaId
      );

      currentScene = Scene(props);
      currentScene.start();
    }
  };
};
